.point::after {
  content: "";
  position: absolute;
  display: block;
  width: 85%;
  max-width: 50rem;
  height: 97%;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: -1;
  background: linear-gradient(var(--g-angle), var(--g-color-0) var(--g-position-0), var(--g-color-1) var(--g-position-1));
  border-radius: 25px;
  --g-color-0: rgb(138, 213, 215);
  --g-position-0: 0%;
  --g-color-1: #9cddbe;
  --g-position-1: 100%;
  --g-color-2: #9cddbe;
  --g-position-2: 100%;
  --g-color-3: #9cddbe;
  --g-position-3: 100%;
  --g-color-4: #9cddbe;
  --g-position-4: 100%;
  --g-color-5: #9cddbe;
  --g-position-5: 100%;
  --g-color-6: #9cddbe;
  --g-position-6: 100%;
  --g-color-7: #9cddbe;
  --g-position-7: 100%;
  --g-color-8: #9cddbe;
  --g-position-8: 100%;
  --g-color-9: #9cddbe;
  --g-position-9: 100%;
  --g-color-10: #9cddbe;
  --g-position-10: 100%;
  --g-color-11: #9cddbe;
  --g-position-11: 100%;
  --g-angle: 94deg;
}
